/* Blanco.css */
body,
html {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
}

.container {
  display: grid;
  grid-template-columns: 55% 45%;
  height: 93vh;
}

.left-column,
.right-column {
  display: grid;
}

.left-column {
  grid-template-rows: 27vh 66vh;
  width: 100%;

}

.right-column {
  grid-template-rows: 40vh 60vh;
  width: 100%;
  position: relative;
}

.right-top {
  background-color: #fdfdfd;
  z-index: 2;
  margin-left: 20%;
  display: grid;
  align-items: center;
  grid-template-columns: 70% 30%;
  gap: 10px;
}

.right-bottom {
  background-color: #040405;
  z-index: 1;
  position: absolute;
  height: 93vh;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  padding: 5%;
  width: 100%;
  box-sizing: border-box;
}

.left-top,
.left-bottom {

  /* border-top: none; */
  display: flex;
  align-items: center;
  justify-content: center;
}

.left-top {
  flex-direction: row;
  justify-content: space-around;
}

.left-bottom {
  border-top: 1px solid #000;
  border-bottom: 1px solid #000;
  background-color: #fdfdfd;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  padding: 5%;
  width: 100%;
  box-sizing: border-box;
}

.left-bottom .content {
  flex-direction: column;
  align-items: flex-start;
  width: 100%;
  max-width: 600px;
  box-sizing: border-box;
}

.left-top-img,
.right-top-img {
  border-radius: 1px;
  object-fit: cover;
  width: 30%;
  height: 90%;
}

.right-top-img {
  width: 95%;
  height: 100%;
  display: block;
  box-sizing: border-box;
  overflow: hidden;
}

/* Nueva clase para manejar la imagen de fondo */
.background-image {
  width: 100%;
  height: 100%;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center; /* Puedes cambiar esto según sea necesario */
}

.right-top {
  background-color: #fdfdfd;
  z-index: 2;
  display: flex;
  justify-content: center;
  width: 80%;
  box-sizing: border-box; /* Asegura que el padding se incluya en el tamaño total */
}







.vertical-rectangle {
  /* width: 50%;
  height: 80%; */
  width: 30%;
  height: 90%;
}

.gallery-button {
  background-color: #000;
  color: #fff;
  border: none;
  padding: 20px 30px;  ;
  cursor: pointer;
  margin-top: 10px;
}

.right-top-left {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 57%;
  height: 90%;
  gap: 15px;
  box-sizing: border-box; /* Asegura que el padding se incluya en el tamaño total */
  overflow: hidden; /* Asegura que el contenido no se desborde */
}

h3,
h2 {
  margin: 0;
  font-family: "Nunito Sans", sans-serif;
}

.left-bottom h3 {
  color: #000;
  font-size: 2.3rem;
  margin-bottom: 20px;
  font-weight: lighter;
  text-align: left;
}

.right-bottom h2 {
  color: white;
  font-size: 2.2rem;
  margin-top: 40vh;
  font-weight: lighter;
  text-align: left;
  width: 70%;
  /* padding-right: 25px; */
}

.right-bottom h3 {
  color: white;
  font-size: 1.2rem;
  margin-top: 5vh;
  font-weight: lighter;
  text-align: left;
  width: 60%;
  padding-right: 25px;
}

/* Media queries para dispositivos móviles */
@media (max-width: 1500px) {




  .right-top-left {
    margin: 0;
    gap: 15px;
  }
  .right-bottom h3 {
    font-size: 1.3rem;
  }
}

@media (max-width: 1100px) {


  .left-bottom {
    width: 100%;
  }
  .left-bottom h3 {
    font-size: 1.8rem;
  }
  .rectangle {
    display: none;
  }
  .left-top-img, .right-top-img {

    width: 45%;
  }
}

@media (max-width: 768px) {
  .right-top {
    grid-template-columns: 100% 0%;
  }
  .vertical-rectangle {
    display: none;
  }
  .left-top {
    justify-content: center;
    gap: 15px;
    width: 100%;

  }

  .left-bottom h3 {
    font-size: 1.2rem;
  }
  .left-bottom {
    padding: 15%;
    width: 100%;
    /* border: none; */
  }
  .gallery-button {
    padding: 10px 20px;
  }
  .right-bottom h2 {
    width: initial;
    font-size: 1.5rem;
  }
  .right-bottom h3 {
    width: initial;
    font-size: 1.2rem;
  }
}

@media (max-width: 500px) {

  .small-square:nth-of-type(1) {
    display: none;
  }

}

@media (width: 430px) and (height: 932px) {
  .right-bottom h2 {
    color: white;
    font-size: 2rem;
    padding-right: 15px;
  }
}

@media (max-width: 500px) {


  .left-bottom h3 {
    font-size: 1.2rem;
  }
  .left-bottom {
    padding: 15%;
    width: inherit;
    border-left: none;
  }
  .right-bottom h2 {
    font-size: 1.2rem;
  }
  .right-bottom h3 {
    width: initial;
    font-size: 1rem;
  }
  .right-top {
    margin-left: 0%;
    width: 100%;
    border-left: 1px solid #000  }
  .container {
    height: 100vh;
  }
  .left-column {
    grid-template-rows: 30vh 70vh;
  }
  .right-bottom {
    height: 100vh;
  }
  .left-top-img, .right-top-img {
    width: 85%;
    height: 85%;
    
}
.right-top-left {

  width: 85%;
  height: 85%;
  gap: 4.4%;

}}

