.negro-container {
  display: grid;
  grid-template-columns: 45% 55%;
  height: 93vh;
}

.negro-left-column {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #000;
  color: #fff;
  padding: 20px;
  overflow: hidden; /* Asegura que el contenido no se desborde */
  font-family: "Nunito Sans", sans-serif;
  font-weight: lighter;
  box-sizing: border-box; /* Asegura que el padding no afecte el tamaño total */
  flex-direction: column; /* Mantiene el contenido en columna */
}

.negro-text-grid {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  text-align: left;
  width: 70%;
  max-width: 100%; /* Evita que el contenedor sea más ancho que su padre */
  padding-right: 20px;
  box-sizing: border-box; /* Asegura que el padding no afecte el tamaño total */
  overflow: hidden; /* Evita el desbordamiento del texto */
}

/* Ajustes para el texto */
.negro-text-grid h1 {
  color: white;
  font-size: 1.8rem;
  font-weight: lighter;
  margin: 0; /* Elimina el margen para evitar el desbordamiento */
  overflow-wrap: break-word; /* Ajusta el texto largo */
}

.negro-text-grid h3 {
  /* margin: 10px 0; */
  margin: 3% 0;
  color: white;
  font-size: 1.2rem;
  font-weight: lighter;
  overflow-wrap: break-word; /* Ajusta el texto largo */
}

.negro-right-column {
  display: flex;
  flex-direction: column;
  align-items: end;
  justify-content: space-around;
  width: 100%;
  box-sizing: border-box;
}

.negro-right-top {
  display: flex;
  flex-direction: column;
  align-items: center; /* Centra las imágenes horizontalmente */
  gap: 20px; /* Ajusta el espacio entre las imágenes aquí */
}

.negro-right-top-top,
.negro-right-top-bottom {
  display: flex;
  justify-content: center;
  align-items: center;
  background-size: cover; /* Asegura que la imagen cubra todo el contenedor */
  background-position: center; /* Centra la imagen */
  box-sizing: border-box;
  border-radius: 5px 0px 0px 5px;
}

.negro-right-top-top {
  background-image: url('./img/parque.jpeg'); /* Ruta de la imagen superior */
  width: 60%;
  height: 40%;
}

.negro-right-top-bottom {
  background-image: url('./img/puertas.jpeg'); /* Ruta de la imagen inferior */
  width: 80%;
  height: 40%;
}

/* Media Queries */
@media (max-width: 1500px) {
  /* Estilos responsivos si es necesario */
}

@media (max-width: 1440px) {
  .negro-text-grid {
    width: 65%;
  }
  .negro-text-grid h1 {
    font-size: 1.3rem;
  }
  .negro-text-grid h3 {
    font-size: 1.1rem;
  }
}

@media (max-width: 1024px) {
  .negro-text-grid {
    width: 80%;
  }
  .negro-text-grid h1 {
    font-size: 1.5rem;
  }
  .negro-text-grid h3 {
    font-size: 1rem;
  }
}

@media (max-width: 768px) {
  .negro-text-grid {
    width: 100%;
  }
  .negro-text-grid h1 {
    font-size: 1.3rem;
  
  }
  .negro-text-grid h1:nth-of-type(-n+1),
.negro-text-grid h3:nth-of-type(-n+1) {
  display: none; /* Oculta los últimos dos elementos h1 y h3 */
}
}

@media (max-width: 480px) {
  .negro-text-grid {
    padding-right: 10px; /* Reduce padding para pantallas muy pequeñas */
    width: 100%;
  }
  .negro-text-grid h1 {
    font-size: 1.1rem;
  }
  .negro-text-grid h3 {
      font-size: 0.8rem;
      margin: 5% 0;
  }

  .negro-container {
    height: 100vh;
  }
}

@media (max-width: 380px) {

  .negro-text-grid h3 {
    font-size: 0.65rem;
  }
  .negro-text-grid h1{
    font-size: 1rem;

  }
}
