/* Contenedor principal */
.cronicas-container {
  display: grid;
  grid-template-columns: 1fr 1fr; /* Dos columnas iguales */
  min-height: 70vh; /* Altura mínima para el contenedor */
  width: 100%;
  box-sizing: border-box;
}

/* Columna izquierda */
.cronicas-left-column {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: left;
  padding: 20px; /* Espaciado */
}

/* Título principal */
.cronicas-left-column h1 {
  font-size: 2rem;
  font-family: "Nunito Sans", sans-serif;
  margin-bottom: 20px;
  width: 90%;
}

/* Subtítulo */
.cronicas-left-column h3 {
  font-family: "Nunito Sans", sans-serif;
  font-weight: lighter;
  font-size: 1rem;
  width: 90%;
}

/* Columna derecha */
.cronicas-right-column {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px; /* Espaciado */
}

/* Grid para las imágenes */
.cronicas-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr); /* Dos columnas */
  grid-template-rows: repeat(2, 1fr); /* Dos filas */
  gap: 10px; /* Espaciado entre las imágenes */
  width: 100%;
}

/* Elemento del grid con imagen de fondo */
.cronicas-grid-item {
  background-size: cover;
  background-position: center;
  height: 200px; /* Altura fija para las imágenes */
  box-sizing: border-box;
}

/* Imágenes de fondo específicas */
.cronicas-grid-item:nth-child(1) { background-image: url('./img/ruido1.jpeg'); }
.cronicas-grid-item:nth-child(2) { background-image: url('./img/ruido2.jpeg'); }
.cronicas-grid-item:nth-child(3) { background-image: url('./img/ruido3.jpeg'); }
.cronicas-grid-item:nth-child(4) { background-image: url('./img/ruido4.jpeg'); }

/* Estilos responsivos */
@media (max-width: 1200px) {
  .cronicas-left-column h1 {
    font-size: 1.5rem;
  }
  
  .cronicas-left-column h3 {
    font-size: 0.9rem;
  }

  .cronicas-grid-item {
    height: 180px;
  }
}

@media (max-width: 992px) {
  .cronicas-left-column {
    padding: 15px;
  }

  .cronicas-left-column h1 {
    font-size: 1.25rem;
  }

  .cronicas-left-column h3 {
    font-size: 0.85rem;
  }

  .cronicas-grid-item {
    height: 160px;
  }
}

@media (max-width: 768px) {


  .cronicas-left-column {
    text-align: center;
    padding: 10px;
  }

  .cronicas-left-column h1 {
    font-size: 1.1rem;
  }

  .cronicas-left-column h3 {
    font-size: 0.8rem;
  }

  .cronicas-grid {
    grid-template-columns: 1fr; /* Una sola columna */
    grid-template-rows: repeat(4, 1fr); /* Cuatro filas */
  }

  .cronicas-grid-item {
    height: 140px;
  }
}

@media (max-width: 576px) {

  .cronicas-left-column h1 {
    font-size: 1rem;
  }

  .cronicas-left-column h3 {
    font-size: 0.75rem;
  }

  .cronicas-grid-item {
    height: 120px;
  }
}
